<template>
  <div class="layout">
    <div class="layout-left" v-if="isShowLeft"
         :class="isDrag ? '' : (isExpand ? 'expand layout-left-border': 'collapse')"
         :style="{width: `${customWidth}px`}">
      <div class="layout-left-content">
        <slot name="left"></slot>
      </div>
      <div class="layout-left-bottom">
        <div v-if="isExpand">
          <img src="../../assets/.png" alt="" title="" @click="handleClick('')">
        </div>
        <div v-else>
          <img src="../../assets/.png" alt="" title="" @click="handleClick('')">
        </div>
      </div>
    </div>
    <div class="y-resize" title="" v-if="isShowLeft && isExpand"/>
    <div class="layout-center" v-if="isShowLeft">
      <div class="expand-btn"  title="" v-if="isExpand" @click="handleClick('')">
        <i class="el-icon-d-arrow-left"></i>
      </div>
      <div class="expand-btn"  title="" v-else @click="handleClick('')">
        <i class="el-icon-d-arrow-right"></i>
      </div>
    </div>
    <div class="layout-right"
         :class="rightClass">
      <div class="layout-right-container">
        <div class="slot-right-container">
          <slot name="right"/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      isExpand: true,
      isDrag: false,
      customWidth: 0
    };
  },
  name: 'layout',
  props: {
    width: {
      type: Number,
      default: 256
    },
    isShowLeft: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    rightClass () {
      return !this.isShowLeft ? 'just-right' : (this.isExpand ? 'expand-right' : 'collapse-right');
    }
  },
  mounted () {
    // 0
    // store.commit('SET_MOVE_LEN', 0);
    this.customWidth = this.width;
    this.handleYResize();
  },
  methods: {
    /**
     * @Description 
     * @author qianyinggenian
     * @date 2023/9/15
     */
    handleClick (flag) {
      this.isExpand = !this.isExpand;
      if (flag === '') {
        this.customWidth = 0;
      } else {
        this.customWidth = this.width;
      }
      this.$emit('moveLen', 0);
      // store.commit('SET_MOVE_LEN', 0);
    },
    /**
     * @Description 
     * @author qianyinggenian
     * @date 2023/9/15
     */
    handleYResize () {
      const box = document.getElementsByClassName('layout');
      const left = document.getElementsByClassName('layout-left');
      const resize = document.getElementsByClassName('y-resize');
      const right = document.getElementsByClassName('layout-right');
      const that = this;
      // const _store = store;
      for (let i = 0; i < resize.length; i++) {
        // 
        resize[i].onmousedown = function (e) {
          // 
          resize[i].style.background = '#818181';
          const startX = e.clientX;
          resize[i].left = resize[i].offsetLeft;
          if (that.isExpand) {
            // 
            document.onmousemove = function (e) {
              that.isDrag = true;
              const endX = e.clientX;
              let moveLen = resize[i].left + (endX - startX); // endX-startX=resize[i].left+=
              // const maxT = box[i].clientWidth - resize[i].offsetWidth; //  -  = 
              if (moveLen < that.width) moveLen = that.width; // left256px
              // if (moveLen > maxT - that.width) moveLen = maxT - that.width; // right256px
              if (moveLen > that.width * 2) moveLen = that.width * 2; // right512px

              resize[i].style.left = moveLen; // left
              that.customWidth = moveLen;
              let treeNodeWidth = 0;
              if (treeNodeWidth < 0) {
                treeNodeWidth = that.width;
              }
              if (moveLen > that.width) {
                // treeNodeWidth = moveLen - that.width - 50;
                treeNodeWidth = moveLen - that.width;
              }
              that.$emit('moveLen', treeNodeWidth);
              // _store.commit('SET_MOVE_LEN', treeNodeWidth);
              for (let j = 0; j < left.length; j++) {
                left[j].style.width = moveLen + 'px';
                right[j].style.width = (box[i].clientWidth - moveLen - 3) + 'px';
              }
            };
          }
          // 
          document.onmouseup = function (evt) {
            // 
            resize[i].style.background = '#51D1FF';
            document.onmousemove = null;
            document.onmouseup = null;
            that.isDrag = false;
            resize[i].releaseCapture && resize[i].releaseCapture(); // ReleaseCapture()
          };
          resize[i].setCapture && resize[i].setCapture(); // 
          return false;
        };
      }
    }
  }
};
</script>
<style lang="scss" scoped>
$color: #51D1FF;
.layout {
  width: 100%;
  height: 100%;
  display: flex;
  box-sizing: border-box;

  .layout-left, .y-resize, .layout-right {
    float: left;
    overflow: hidden;
  }

  .layout-left {
    width: 256px;
    height: 100%;
    background: #0A1A34;
    box-shadow: inset 0 1px 12px 0 rgba(81, 209, 255, 0.302);
    border-radius: 0 0 0 0;
    opacity: 1;
    box-sizing: border-box;
    position: relative;
    .layout-left-content {
      height: calc(100% - 43px);
      width: 100%;
      overflow: auto;
    }

    .layout-left-bottom {
      height: 43px;
      width: 100%;
      border: 1px solid;
      border-image: linear-gradient(180deg, rgba(81.01349964737892, 209.00092989206314, 255, 0.30000001192092896), rgba(81.01349964737892, 209.00092989206314, 255, 0.5), rgba(81.01349964737892, 209.00357455015182, 255, 0.30000001192092896)) 1 1;

      > div {
        margin-top: 12px;
        width: 22px;
        height: 22px;
        margin-left: 16px;

        img {
          cursor: pointer;
        }
      }
    }
  }
  .layout-left-border {
    border-top: 1px solid $color;
    border-left: 1px solid $color;
    border-bottom: 1px solid $color;
  }
  .layout-center {
    height: 100%;
    display: flex;
    width: 20px;
    align-items: center;
    justify-content: center;
    .expand-btn {
      height: 100px;
      width: 20px;
      cursor: pointer;
      background: $color;
      position: relative;
      border-top-right-radius: 11px;
      border-bottom-right-radius: 11px;
      display: flex;
      justify-content: center;
      align-items: center;
      i {
        font-size: 16px;
      }
      &::before,&::after {
        content: '';
        position: absolute;
        width: 20px;
        height: 20px;
      }
      &::before {
        top: -20px;
        right: 0;
        background: radial-gradient(
                circle at 100% 0%,
                transparent 21px,
                $color 23px
        );
      }
      &::after {
        bottom: -20px;
        right: 0;
        background: radial-gradient(
                circle at 100% 100%,
                transparent 21px,
                $color 23px
        );
      }
    }
  }
  .y-resize {
    width: 2px;
    height: 100%;
    background: $color;
    cursor: w-resize;
  }

  .layout-right {
    width: calc(100% - 256px - 3px);
    height: 100%;
    background: #060d24;

    .layout-right-container {
      overflow: hidden;
      height: 100%;
      background: #060d24;
      opacity: 1;
      padding: 20px;
      box-sizing: border-box;

      .slot-right-container {
        width: 100%;
        height: 100%;
        padding: 20px;
        box-sizing: border-box;
        box-shadow: inset 0 1px 12px 0 rgba(3, 251, 255, 0.3);
        border: 1px solid rgba(81, 209, 255, 0.7);
        background-color: #0a1a34;
        border-radius: 8px;

        > div {
          height: 100%;
          display: flex;
          flex-flow: column;
        }
      }
    }
  }

  .collapse {
    width: 100px;
    transition: all 0.5s;
  }

  .expand {
    width: 256px;
    transition: all 0.5s;
  }

  .expand-right {
    transition: all 0.5s;
    width: calc(100% - 256px - 3px) !important;
  }

  .collapse-right {
    transition: all 0.5s;
    width: calc(100% - 3px) !important;
  }

  .just-right {
    transition: all 0.5s;
    width: 100% !important;
  }
}
</style>
