    position: absolute;
    right: 0;
    div {
      width: 20px;
      height: 22px;
      background-color: #bae7ff;
      color: black;
      border: 1px solid;
    }
  }

  .left-icon {
    width: 20px;
    left: 0;
    position: absolute;
    div {
      width: 20px;
      height: 22px;
      background-color: #bae7ff;
      color: black;
      border: 1px solid;
    }
  }

  .swiper-scroll-content {
    position: relative;
    white-space: nowrap;
    //overflow: hidden;
    transition: all 0.3s;
    display: flex;
  }
}
