      inputValue: '',
      value1: '2',
      value: ['2', '1'],
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: ''
      }],
      data: [
        {
          id: 1,
          label: 'Level 1 - 1',
          children: [
            {
              id: 2,
              label: 'Level 2 - 1',
              children: [
                {
                  id: 3,
                  label: 'Level 3 - 1',
                  children: [
                    {
                      id: 20,
                      label: 'Level 4 - 1'
                    },
                    {
                      id: 21,
                      label: 'Level 4 - 2'
                    }
                  ]
                },
                {
                  id: 4,
                  label: 'Level 3 - 2'
                }
              ]
            },
            {
              id: 5,
              label: 'Level 2 - 2',
              children: [
                {
                  id: 6,
                  label: 'Level 3 - 3'
                },
                {
                  id: 7,
                  label: 'Level 3 - 4'
                }
              ]
            }
          ]
        },
        {
          id: 8,
          label: 'Level 1 - 2',
          children: [
            {
              id: 9,
              label: 'Level 2 - 3',
              children: [
                {
                  id: 10,
                  label: 'Level 3 - 5'
                },
                {
                  id: 11,
                  label: 'Level 3 - 6'
                }
              ]
            }
          ]
        }
      ],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      defaultExpandedKeys: []
    };
  },
  props: {},
  watch: {},
  computed: {},
  mounted () {
    this.setDefaultExpandedKeys();
  },
  methods: {
    setDefaultExpandedKeys () {
      const keys = [];
      this.data.forEach(item => {
        if (item.children) {
          keys.push(item.id); // 
          item.children.forEach(child => {
            if (child.children) {
              keys.push(child.id); // 
              child.children.forEach(grandChild => {
                keys.push(grandChild.id); // 
              });
            }
          });
        }
      });
      this.defaultExpandedKeys = keys;
    },
    /**
     * @Description
     * @author qianyinggenian
     * @date 2024/6/29
     */
    closeMap () {
      this.isShowTMap = false;
    },
    showMap () {
      this.isShowTMap = true;
      this.$nextTick(() => {
        this.$refs.tMapRef.getInfo({
          latitude: 23.050014,
          longitude: 113.294975,
          radius: 200
        });
      });
    },
    getThunderLink (href) {
      return `thunder://${btoa(`AA${href}ZZ`)}`;
    },
    addWatermarkAndDownload (imageUrl, watermarkText, outputFilename) {
      // 
      const image = new Image();
      image.crossOrigin = 'Anonymous'; // 
      image.onload = function () {
        // 
        const canvas = document.createElement('canvas');
        canvas.width = image.width;
        canvas.height = image.height;
        const ctx = canvas.getContext('2d');

        // 
        ctx.font = '50px Arial';
        // ctx.fillStyle = 'rgba(255, 255, 255, 0.5)'; // 
        ctx.fillStyle = '#000000'; // 
        ctx.fillText(watermarkText, 50, 50); // (50, 50)

        // 
        ctx.drawImage(image, 0, 0, image.width, image.height);

        // 
        const dataUrl = canvas.toDataURL('image/png');
        const link = document.createElement('a');
        link.href = dataUrl;
        link.download = outputFilename || 'watermarked.png';
        link.click();
      };
      image.src = imageUrl;
    },
    handleClear () {
      console.log(111111);
    },
    handleClick (vm) {
      // this.$message({
      //   type: 'error',
      //   message: '/',
      //   duration: 0,
      //   showClose: true
      // });
      this.$message({
        type: 'success',
        message: '/',
        duration: 5000
      });
    },
    /**
     * @Description 
     * @author qianyinggenian
     * @date 2024/01/08
    */
    handleChange (event) {
      const file = event.target.files[0];
      // 
      console.log(':', file.name);
      console.log(':', file.type);
      const reader = new FileReader();
      reader.onloadend = () => {
        console.log(':', reader.result);
        this.getFileInfo(reader.result);
      };
      reader.readAsDataURL(file);
    },
    /**
     * @Description 
     * @author qianyinggenian
     * @date 2024/01/08
    */
    getFileInfo (path) {
      axios.get(path) // 
        .then(response => {
          this.fileContent = response.data;
          console.log('fileContent', this.fileContent);
        })
        .catch(error => {
          console.log('Error:', error);
        });
    }
  }
};
