
<template>
  <div>

    <div class="test2">
      <div class="left">
        <el-button @click="add"></el-button>
        <el-button @click="subtract"></el-button>
        <div class="item" v-for="item in count" :key="item"></div>
      </div>
      <div class="right">

        <div class="item2" v-for="item in 50" :key="item"></div>
      </div>
    </div>
  </div>
</template>
<script>
import { listenMsg } from '@/utils/util';
export default {
  components: {},
  data () {
    return {
      count: 2
    };
  },
  mounted () {
    this.removeListen = listenMsg((info) => {
      console.log('info', info);
      if (info.msg) {
        this.count = info.msg;
      }
    });
  },
  destroyed () {
    this.removeListen();
  },
  methods: {
    add () {
      this.count += 1;
    },
    subtract () {
      if (this.count > 0) {
        this.count -= 1;
      }
    }
  }
};
</script>
