import Cookies from 'js-cookie';

export const yiAnToken = 'yiAnToken';
export const refreshTokenKey = 'refreshTokenKey';
export const tokenExpireTime = 24 * 60 * 60 * 1000; // 31
export const refreshTokenExpireTime = 23.5 * 60 * 60 * 1000; // 

/**
 * @Description Cookie
 * @author qianyinggenian
 * @date 2023/9/26
 */
export function getToKen () {
  return Cookies.get(yiAnToken);
}

/**
 * @Description Cookie
 * @author qianyinggenian
 * @date 2023/9/26
 */
export function getRefreshToken () {
  return Cookies.get(refreshTokenKey);
}

/**
 * @Description Cookie
 * @author qianyinggenian
 * @date 2023/9/26
 */
export function setToken (name, value) {
  const expires = new Date(new Date() * 1 + tokenExpireTime);
  Cookies.set(name, value, { expires: expires }); // 10 
}

/**
 * @Description Cookie
 * @author qianyinggenian
 * @date 2023/9/26
 */
export function removeToken () {
  Cookies.remove(yiAnToken);
  Cookies.remove(refreshTokenKey);
}

export function setRefreshToken (Token) {
  const rfExpireTime = new Date(new Date().getTime() + refreshTokenExpireTime); // 
  Cookies.set(refreshTokenKey, Token, { expires: rfExpireTime }); // cookie
}
