/**
 * 
 * @param {object} db 
 * @param {string} storeName 
 * @param {object} data 
 */
export function updateDB (db, storeName, data) {
  return new Promise((resolve, reject) => {
    var request = db
      .transaction([storeName], 'readwrite') // 
      .objectStore(storeName) // 
      .put(data);

    request.onsuccess = () => {
      resolve(200);
    };

    request.onerror = () => {
      // eslint-disable-next-line prefer-promise-reject-errors
      reject(500);
    };
  });
}

// 
export function deleteDB (db, storeName, id) {
  return new Promise((resolve, reject) => {
    var request = db
      .transaction([storeName], 'readwrite')
      .objectStore(storeName)
      .delete(id);

    request.onsuccess = function () {
      //   console.log("");
      resolve({ code: 200, msg: '' });
    };

    request.onerror = function () {
      //   console.log("");
      // eslint-disable-next-line prefer-promise-reject-errors
      reject({ code: 500, msg: '' });
    };
  });
}

// 
export function batchDeleteDB (db, storeName, ids) {
  return new Promise((resolve, reject) => {
    var count = 0;

    for (const id of ids) {
      var request = db
        .transaction([storeName], 'readwrite')
        .objectStore(storeName)
        .delete(id);
      request.onsuccess = function () {
        ++count;
        if (count === ids.length) {
          resolve({
            code: 200,
            msg: ''
          });
        }
      };

      request.onerror = function () {
        // eslint-disable-next-line prefer-promise-reject-errors
        reject({
          code: 500,
          msg: ''
        });
      };
    }
  });
}
